.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

@media(max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}

.form__input {
    padding: 10px;
    border: 1px solid #ddd;
}

.form__button {
    padding: 10px;
    background-color: hsl(34, 39%, 45%);
    color: white;
    border: none;
    transition: 0.4s;
}

.form__button:hover {
    background-color: hsl(34, 39%, 50%);
    transform: scale(1.1);
}